import PropTypes from 'prop-types'

function TriColumn({ content, dark }) {
    return (
        <div className={`tricolumn-container row ${dark ? 'dark' : ''}`}>
            { content.map((content, index) => (
                <div className="info-column col-sm-4" key={ content + '-' + index }>
                    <h3 className="column-header">{ content.header }</h3>
                    <p className="column-text">{ content.body }</p>
                </div>
            ))}
        </div>
    )
}

TriColumn.propTypes = {
    content: PropTypes.array,
    dark: PropTypes.bool
}

export default TriColumn

