import React, { useRef } from 'react'
import emailjs from 'emailjs-com'

function ContactForm() {
    const form = useRef()

    const sendEmail = (e) => {
        e.preventDefault()

        emailjs.sendForm('service_1egr73o', 'template_ebtltsr', form.current, 'user_nf4wRX3OlYblR3h8NhLCb')
        .then((result) => {
            console.log(result.text)
        }, (error) => {
            console.log(error.text)
        });
    };

    return (
        <form ref={ form } onSubmit={ sendEmail }>
            <div className="form-group row">
                <label className="col-sm-4 col-form-label" htmlFor="contact-name">Name</label>
                <div className="col-sm-8">
                    <input id="contact-name" required name="contact_name" type="text" className="form-control" placeholder="John Smith" />
                </div>
            </div>
            <div className="form-group row">
                <label className="col-sm-4" htmlFor="contact-email">Email Address</label>
                <div className="col-sm-8">
                    <input id="contact-email" required name="contact_email" type="email" className="form-control" aria-describedby="contact-help" placeholder="johnsmith@mail.com" />
                    
                </div>
            </div>
            <div className="form-group row">
                <label className="col-sm-4" htmlFor="contact-phone">Phone Number</label>
                <div className="col-sm-8">
                    <input id="contact-phone" name="contact_phone" type="tel" className="form-control" aria-describedby="contact-help" placeholder="1(888)888-8888" />
                </div>
            </div>
            <div className="form-group row">
                <label className="col-sm-4 col-form-label" htmlFor="contact-message">Message</label>
                <div className="col-sm-8">
                    <textarea id="contact-message" name="contact_message" type="text" rows="4" className="form-control" placeholder="Hi, I am interested in using inBuild..." />
                </div>
            </div>
            <button type="submit" data-bs-toggle="modal" data-bs-target="#contact-modal" className="btn btn-primary">Get In Touch</button>
        </form>
    )
}

export default ContactForm

