import PropTypes from 'prop-types'
import React, { useState } from 'react';
import { Document, Page } from 'react-pdf/dist/umd/entry.webpack';

import './PdfEmbed.scss'

function PrivacyPolicy({ header, file }) {
    const [numPages, setNumPages] = useState(null);
    const [pageNumber, setPageNumber] = useState(1);
    const [windowWidth, setWindowWidth] = useState(window.innerWidth - (window.innerWidth * 0.10));
  
    function onDocumentLoadSuccess({ numPages }) {
      setNumPages(numPages);
      setPageNumber(1);
    }
  
    function changePage(offset) {
      setPageNumber(prevPageNumber => prevPageNumber + offset);
    }
  
    function previousPage() {
      changePage(-1);
    }
  
    function nextPage() {
      changePage(1);
    }

    function handleResize() {
      setWindowWidth(window.innerWidth - (window.innerWidth * 0.10));
    }

    window.addEventListener('resize', handleResize)

    return (
        <div className="pdf-container">
            <div className="pdf-toolbar">
              <h2 className="legal-header">{ header }</h2>
              <div className="toolbar-row">
                <button className="btn btn-primary" type="button" disabled={ pageNumber <= 1 } onClick={ previousPage }><i className="fas fa-chevron-left"/></button>
                <p>Page { pageNumber || (numPages ? 1 : '--') } of { numPages || '--' }</p>
                <button className="btn btn-primary" type="button" disabled={ pageNumber >= numPages } onClick={ nextPage }><i className="fas fa-chevron-right"/></button>
              </div>
            </div>
            <Document
              className="pdf-document"
              file={ file }
              onLoadSuccess={ onDocumentLoadSuccess }
            >
              <Page pageNumber={ pageNumber } width={ windowWidth } />
            </Document>
        </div>
    )
}

PrivacyPolicy.propTypes = {
  header: PropTypes.string,
  file: PropTypes.string
}

export default PrivacyPolicy

