import { Link } from "react-router-dom"

// import inBuildLogo from '../../assets/ib_logo.png'

function Legal() {
    return (
        <div className="legal-information">
            {/* <img className="legal-item" src={ inBuildLogo } /> */}
            <p className="legal-item copyright">&copy;{ new Date().getFullYear() } inBuild</p>
            <Link to="/privacy" className="legal-item legal-link">Privacy Policy</Link>
            <Link to="/terms" className="legal-item legal-link">Terms of Service</Link>
        </div>
    )
}

export default Legal

