import React from 'react'
import PropTypes from 'prop-types'

function Image({ src, alt }) {
    return (
        <div className="image-container col-sm-8">
            <img src={ src } alt={ alt }/>
        </div>
    )
}

Image.propTypes = {
    src: PropTypes.string,
    alt: PropTypes.string
}

export default Image

