import ContactForm from './ContactForm'

import './ContactForm.scss'

function ContactModal() {
    return (
        <div className="modal fade" id="contact-modal" tabIndex="-1" role="dialog" aria-labelledby="contact-modal-title" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="contact-modal-title">Contact Us - Request a Demo</h5>
                        <button type="button" className="btn btn-primary" data-bs-dismiss="modal" aria-label="Close">
                            <i className="fas fa-times"></i>
                        </button>
                    </div>
                    <div className="modal-body">
                        <p className="contact-welcome">Get in touch with us today about scheduling a demo or getting more detailed information.</p>
                        <ContactForm />
                    </div>
                    <div className="modal-footer">
                        <small id="contact-help" className="text-muted">We'll never share your personal information.</small>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ContactModal

