import PropTypes from 'prop-types'

function Description({ header, body, subheadings }) {
    return (
        <div className="hero-description-container">
            <h2 className="hero-header">{ header }</h2>
            <p className="hero-subtext">{ body }</p>
            
            <div className="hero-subheaders row">
                { subheadings.map((subheading, index) => (
                    <div className="hero-subheading-container col-sm-3" key={ subheading + '-' + index }>
                        <h4 className="hero-subheading">{ subheading.header }</h4>
                        <p className="hero-subheading-body">{ subheading.body }</p>
                    </div>
                ))}
            </div>
        </div>
    )
}

Description.propTypes = {
    header: PropTypes.string,
    subtext: PropTypes.string,
    tagline: PropTypes.string
}

export default Description

