import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from 'react-router-dom';

import ReactGA from 'react-ga4';

import Navbar from './components/Navbar';
import Hero from './components/hero/Hero';
import Breakout from './components/breakout/Breakout';
import Subheader from './components/Subheader';
import Content from './components/content/Content';
import TriColumn from './components/TriColumn';
import Footer from './components/footer/Footer';
import ContactModal from './components/contactForm/ContactModal';
import PdfEmbed from './components/legal/PdfEmbed';

import privacyFile from './assets/pdf/inBuild_Privacy_Policy.pdf';
import termsFile from './assets/pdf/inBuild_Terms_of_Service.pdf';

import homeImg1 from './assets/homepage_img_01.png';
import homeImg2 from './assets/homepage_img_02.png';
import productImg1 from './assets/product_img_01.png';
import productImg2 from './assets/product_img_02.png';

function App() {
  ReactGA.initialize('G-Q0TQGQXMXR');
  ReactGA.send("pageview");

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="container-fluid">
        <ContactModal />
        <Navbar />

        <Switch>
          <Route exact path="/">
            <Home />
          </Route>

          <Route exact path="/product">
            <Product />
          </Route>

          <Route exact path="/privacy">
            <Privacy />
          </Route>

          <Route exact path="/terms">
            <Terms />
          </Route>

          <Route path="/apps">
            <Redirect to="/"/>
          </Route>

          <Route path="/account">
            { window.location.href = "https://app.inbuild.ai" }
          </Route>
        </Switch>

        <Footer
          menus={[
            {
              title: "Information",
              links: [
                {
                  nav: true,
                  href: "/",
                  text: "Home"
                },
                {
                  nav: true,
                  href: "/product",
                  text: "About inBuild"
                }
              ]
            },
            {
              title: "inBuild Access",
              links: [
                {
                  href: "https://app.inbuild.ai/account/login",
                  text: "Sign In"
                },
                {
                  modal: true,
                  href: "",
                  text: "Request a Demo"
                }
              ]
            }
          ]}
          social={[
            {
              href: "https://www.linkedin.com/company/inbuildai",
              faIcon: "linkedin"
            },
            {
              href: "https://www.twitter.com",
              faIcon: "twitter"
            }
          ]}
        />
      </div>
    </Router>
  );
}

function Home() {
  return (
    <div className="homepage">
      <Hero
        header="Software that makes inputting invoices easy with AI."
        subtext="By using our interface to review and approve invoices, you can reduce the time and cost of processing them to less than one minute per invoice, by removing the need for repetitive manual input."
        subheaders={[
          {
            header: "Automation",
            body: "Connect Your AP Email Address"
          },
          {
            header: "Accuracy",
            body: "97.5% Confidence Threshold"
          },
          {
            header: "Integration",
            body: "Quickbooks, Sage 100, Procore"
          }
        ]}
        btnText="Request a Demo"
        btnClass="btn-primary"
      />
      <Breakout
        header="Accounting software that works as hard as you do"
        featuresList={['Simple, reliable, and secure', 'Easily manage cash flow', ' Be ready for tax time']}
        subtext="Never miss another detail. Time consuming and complex accounting is a task of the past."
      />
      <Content
        flip
        header="inBuild harnesses the power of artificial intelligence to capture all incoming financial documents."
        content={[
          {
            header: "",
            body: "On average, the quickest a trained bookkeeper can process one invoice into an ERP software is five minutes. By implementing our inBuild invoice process algorithm, we can cut down the time by 92%."
          },
          {
            header: "",
            body: "Instead of processing 12 invoices per hour using manual entry, try our algorithm and account for over 150 invoices in that same amount of time."
          }
        ]}
        image={homeImg1}
      />
      <TriColumn
        content={[
          {
            header: "Robust Algorithm",
            body: "Automate the invoice and accounting processes using machine learning. The invoice approval app provides your project managers and accountants with a simple interface for reviewing and approving invoices."
          },
          {
            header: "Budgeting Optimization",
            body: "Create, adjust, and finalize budgets all in one centralized location. Connect the company data to strengthen each new budget."
          },
          {
            header: "Integration with your ERP",
            body: "Streamlined integration with your enterprise resource planning (ERP) software and your construction management software (CMS)."
          }
        ]}
      />
      <Content
        header="Why inBuild?"
        content={[
          {
            header: "Discover the aspects of inBuild’s software solutions",
            body: "Construction finances are time-consuming, complex, and leave too much room for error. That’s why we developed inBuild."
          }
        ]}
        buttonToggle={true}
        buttonText="Schedule a Demo"
        image={homeImg2}
      />

    </div>
  );
}

function Product() {
  return (
    <div className="product-page">
      <Subheader
        subheader="Invoice processing with ease and accuracy."
        subtext="Where the software solution fits your business."
      />
      <Content
        content={[
          {
            header: "Email Extraction",
            body: "Our intelligent email extraction application allocates for the retrieval of invoice data from your email. Extract the data from each project and all of their invoices and completely automate the input process."
          },
          {
            header: "Detailed Invoice Analysis",
            body: "Once invoices are uploaded you are ready to analyze all the accounting aspects of each project. Get a clear picture of how each job is being paid for without missing any important information."
          },
          {
            header: "Invoice Submittals",
            body: "Each invoice is integrated into the submittal process where the invoice is then assigned to the correct approvers within the company. The invoice then moves on to the accounts payable step in the invoicing lifecycle."
          }
        ]}
        image={productImg1}
      />
      <TriColumn
        dark
        content={[
          {
            header: "Sign Up",
            body: "Speak with us today to set up an account for your business. Begin to save time and money by integrating inBuild."
          },
          {
            header: "Invoice",
            body: "Automate the invoice entry process by directly connecting your AP email address."
          },
          {
            header: "Optimize",
            body: "Improve your team's lead time by reducing the manual labor needed to enter and approve all incoming documents."
          }
        ]}
      />
      <Content
        flip
        content={[
          {
            header: "Approval Processing",
            body: "Depending on your unique company needs there can be multi-step approval flows that require previous approval before requesting a secondary review. This provides greater transparency and accountability."
          },
          {
            header: "Accounts Payable",
            body: "After approval, it is time to pay the vendors and subcontractors. This is where the outstanding amounts will be populated per project for easy accounting. Allowing your AP department to easily sync invoices with their accounting software."
          },
          {
            header: "Specific Project Reporting",
            body: "Each project is going to have unique aspects to analyze. This will give you a detailed overview and reporting data of all of the invoice and accounting aspects for those different projects."
          }
        ]}
        image={productImg2}
      />
    </div>
  );
}

function Privacy() {
  return (
    <PdfEmbed header="Privacy Policy" file={privacyFile} />
  );
}

function Terms() {
  return (
    <PdfEmbed header="Terms of Service" file={termsFile} />
  );
}

export default App;
