import { useState, useEffect } from 'react'
import { Link, NavLink, useLocation } from 'react-router-dom'

import Button from './Button'

import logo from '../assets/inBuild_logo_full.png'

function Navbar() {
    const location = useLocation()
    const [navbar, setNavbar] = useState(false)

    function changeBackground() {
        if(window.scrollY >= 100) {
            setNavbar(true)
        }
        else {
            setNavbar(false)
        }
    }

    useEffect(() => {
        if(location.pathname === '/') {
            setNavbar(false)
    
            window.addEventListener('scroll', changeBackground);
        }
        else {
            setNavbar(true)
        }
      
        return () =>
          window.removeEventListener('scroll', changeBackground);
    }, [location]);

    return (
        <nav className={`navbar sticky-top navbar-expand-lg navbar-dark ${navbar ? 'scrolled' : ''}`}>
            <div className="container-fluid">
                <Link className="navbar-brand" to="/">
                    <img className="navbar-logo img-responsive" src={ logo } alt="inBuild logo" />
                </Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <svg viewBox="0 0 90 75" width="40" height="40">
                        <rect width="90" height="15" rx="5"></rect>
                        <rect y="30" width="90" height="15" rx="5"></rect>
                        <rect y="60" width="90" height="15" rx="5"></rect>
                    </svg>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <NavLink exact to="/" className={`nav-link ${navbar ? 'scrolled' : ''}`} activeClassName="active">Home</NavLink>
                        </li>
                        {/* <li className="nav-item">
                            <NavLink exact to="/blog" className={`nav-link ${navbar ? 'scrolled' : ''}`} activeClassName="active">Blog</NavLink>
                        </li> */}
                        <li className="nav-item">
                            <NavLink exact to="/product" className={`nav-link ${navbar ? 'scrolled' : ''}`} activeClassName="active">Product</NavLink>
                        </li>
                        {/* <li className="nav-item">
                            <NavLink exact to="/about" className={`nav-link ${navbar ? 'scrolled' : ''}`} activeClassName="active">About Us</NavLink>
                        </li> */}
                        <li className="nav-item">
                            <a className={`nav-link ${navbar ? 'scrolled' : ''}`} aria-current="page" href="https://app.inbuild.ai/account/login">Sign In</a>
                        </li>
                        <li className="nav-item">
                            <Button contact text="Request Demo" bsClass="btn-outline-light" />
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}

export default Navbar

