import PropTypes from 'prop-types'

import Feature from './Feature'

function Features({ features }) {
    return (
        <div className="feature-container">
            {features.map((feature, index) => (
                <Feature
                    feature = { feature }
                    key = { feature + '-' + index }
                />
            ))}
        </div>
    )
}

Features.propTypes = {
    features: PropTypes.array
}

export default Features