import PropTypes from 'prop-types'

import './Content.scss'

import Image from './Image'
import TextColumn from './TextColumn'

function Content({ flip, buttonToggle, buttonText, header, content, image }) {
    if(flip) {
        return (
            <div className="content-container row">
                <TextColumn header={ header } content={ content } buttonToggle={ buttonToggle } buttonText={ buttonText } />
                <Image src={ image } alt="temp" />
            </div>
        )
    }
    return (
        <div className="content-container row">
            <Image src={ image } alt="temp" />
            <TextColumn header={ header } content={ content } buttonToggle={ buttonToggle } buttonText={ buttonText } />
        </div>
    )
}

Content.propTypes = {
    flip: PropTypes.bool,
    buttonToggle: PropTypes.bool,
    buttonText: PropTypes.string,
    header: PropTypes.string,
    content: PropTypes.array,
    image: PropTypes.string
}

export default Content

