import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'

function FooterMenu({ width, header, menuItems }) {
    const modalAttributes = {
        'data-bs-toggle': 'modal',
        'data-bs-target': '#contact-modal'
    }

    return (
        <div className={`footer-menu col-sm-${ width }`}>
            <h4 className="menu-header">{ header }</h4>
            <hr />
            { menuItems.map((item, index) => (
                item.nav ? 
                    <Link to={ item.href } className="footer-menu-items" key={ item + '-' + index }>{ item.text }</Link> :
                    <a className="footer-menu-items" href={ item.href } key={ item + '-' + index } {...(item.modal ? {...modalAttributes} : {})}>{ item.text }</a>
            ))}
        </div>
    )
}

FooterMenu.propTypes = {
    width: PropTypes.number,
    header: PropTypes.string,
    menuItems: PropTypes.array
}

export default FooterMenu

