import PropTypes from 'prop-types'
import Button from '../Button'

function TextColumn({ header, content, buttonToggle, buttonText }) {
    return (
        <div className="text-column col-sm-4">
            <h3 className="section-header">{ header }</h3>
            
            { content.map((paragraph, index) => (
                <div className="section-content" key={ paragraph + '-' + index }>
                    { paragraph.header !== '' ? <h4 className="section-subheader">{ paragraph.header }</h4> : '' }
                    <p className="section-text">{ paragraph.body }</p>
                </div>
            ))}

            { buttonToggle ? <Button contact text={ buttonText } bsClass="btn-primary" /> : '' }
        </div>
    )
}

TextColumn.propTypes = {
    header: PropTypes.string,
    content: PropTypes.array,
    buttonToggle: PropTypes.bool,
    buttonText: PropTypes.string
}

export default TextColumn

