import PropTypes from 'prop-types'

import './Breakout.css'

import Features from './Features'

function Breakout({ header, featuresList, subtext }) {
    return (
        <div className="breakout-section">
            {header !== '' ? <h2 className="section-header">{ header }</h2> : ''}
            {featuresList.length > 0 ? <Features features={ featuresList } /> : ''}
            {subtext !== '' ? <h3 className="section-subtext">{ subtext }</h3> : ''}
        </div>
    )
}

Breakout.propTypes = {
    header: PropTypes.string,
    featuresList: PropTypes.array,
    subtext: PropTypes.string
}

export default Breakout

