import PropTypes from 'prop-types'

function Button({ text, bsClass, onClick, contact }) {
    const modalAttributes = {
        'data-bs-toggle': 'modal',
        'data-bs-target': '#contact-modal'
    }

    // This just disables any button that isn't strictly "contact" for the time being, which is fine for now, but also not
    if(!contact)
        modalAttributes.disabled = true

    return (
        <button type="button" className={`btn ${ bsClass }`} onClick={ onClick } { ...modalAttributes } >
            { text }
        </button>
    )
}

Button.propTypes = {
    text: PropTypes.string,
    bsClass: PropTypes.string,
    onClick: PropTypes.func
}

export default Button

